<template>
  <div class="container">
    <!--  -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title=""></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div v-for="obj, index in list" :key="index">
          <!--  -->
          <div class="chat-item left" v-if="obj.name === 'xs'">
            <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
            <div class="chat-pao">{{ obj.msg }}</div>
          </div>

          <!--  -->
          <div class="chat-item right" v-else>
            <div class="chat-pao">{{ obj.msg }}</div>
            <van-image fit="cover" round :src="$store.state.user" />
          </div>
       </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field v-model="word"  placeholder="...">
        <template #button>
          <span  style="font-size:12px;color:#999" @click="sendFn"></span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
import { io } from 'socket.io-client'
import { getToken } from '@/utils/token'
export default {
  name: 'ChatIndex',
  data () {
    return {
      word: '', // 
      // 
      list: [
        //  name 
        { name: 'xs', msg: 'hi' },
        { name: 'me', msg: '' }
      ],
      socket: null
    }
  },
  created () {
    //  websocket 
    this.socket = io('http://toutiao.itheima.net', {
      query: {
        token: getToken()
      },
      transports: ['websocket']
    })
    // 
    this.socket.on('connect', () => {
      console.log('')
    })
    // 
    this.socket.on('message', obj => {
      //  list 
      this.list.push({
        name: 'xs',
        msg: obj.msg
      })
    })
    this.$nextTick(() => {
      this.scrollToBottom()
    })
  },
  //  sock 
  beforeDestroy () {
  // 
    this.socket.close()

    //  websocket 
    this.socket = null
  },
  methods: {
    // 
    sendFn () {
      // 
      if (this.word.trim().length === 0) return

      //  list 
      this.list.push({
        name: 'me',
        msg: this.word
      })

      //  websocket 
      this.socket.emit('message', {
        msg: this.word,
        timestamp: new Date().getTime()
      })
      this.$nextTick(() => {
        this.scrollToBottom()
      })
      // 
      this.word = ''
    },
    // 
    scrollToBottom () {
    //  Item 
      const chatItem = document.querySelectorAll('.chat-item')
      //  DOM 
      const lastItem = chatItem[chatItem.length - 1]
      //  scrollIntoView() 
      lastItem.scrollIntoView({
        behavior: 'smooth' // 
      })
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item {
      padding: 10px;
      .van-image {
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #c2d9ea;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before {
          content: '';
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top: 0.5px solid #c2d9ea;
          border-right: 0.5px solid #c2d9ea;
          background: #e0effb;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 15px;
    &::before {
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 15px;
    margin-right: 0;
    &::before {
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
